<template>
	<div id="Main" style="margin:auto;">
		<div v-show="!FaBu">
			<div id="SelectBlock" v-if="!Main.MapEditer.IsPlayerEdit" style="left:180px;">
				<div id="Select" :style="Main.MapEditer.SelectStyle">
					<img src="/static/images/select.png" draggable="false"/>
				</div>
				<ul v-for="(item,y) in Main.MapEditer.imagedata" >
					<li draggable="false" v-for="(item2,x) in item">
						<img draggable="false" :src="item2" onerror="this.src='/static/images/error.png'" @load="initSelect" @click="SelectBlock(x,y,$event)">
					</li>
				</ul>
			</div>
			<br>
			<div id="MapEditer">
				<ul v-for="(item,y) in Main.MapEditer.data">
					<li draggable="false" v-for="(item2,x) in item" @click="ChangeBlock(x,y)">
						<img draggable="false" :src="item2.url()" onerror="this.src='/static/images/error.png'" @load="initPlayerEditer">
					</li>
				</ul>
			</div>
			<div id="PlayerEditer" v-if="Main.MapEditer.IsPlayerEdit" :style="Main.PlayerEditer.Style">
				<ul v-for="(item,y) in Main.PlayerEditer.data">
					<li draggable="false" v-for="(item2,x) in item" @click="PlayerEdit(x,y,$event)">
						<img draggable="false" :src="item2.url()" onerror="this.src='/static/images/error.png'" v-if="typeof item2.Info[2]!='undefined'">
					</li>
				</ul>
			</div>
			<div id="PlayerInfoEdit" :style="Main.PlayerEditer.EditerStyle" v-if="Main.PlayerEditer.ShowEditer">
					<span>名称:</span><input type="input" v-model="Main.PlayerEditer.PlayerInfo[0]"><br>
					<span>血量:</span><input type="input" v-model.number="Main.PlayerEditer.PlayerInfo[1]"><br>
					<span>类型:</span><select style="width:45.5px" v-model="Main.PlayerEditer.PlayerInfo[2]">
						<option value="dfsb">敌方士兵</option>
						<option value="wfsb">我方士兵</option>
						<option value="dftk">敌方坦克</option>
						<option value="wftk">我方坦克</option>
						<option value="dfhp">敌方火炮</option>
						<option value="wfhp">我方火炮</option>
						<option value="zombie">僵尸</option>
					</select><br>
					<span>阵营:</span><select style="width:45.5px" v-model="Main.PlayerEditer.PlayerInfo[3]">
						<option value="e">敌方</option>
						<option value="f">我方</option>
					</select><br>
					<span>攻距:</span><input type="input" v-model.number="Main.PlayerEditer.PlayerInfo[4]"><br>
					<span>移距:</span><input type="input" v-model.number="Main.PlayerEditer.PlayerInfo[5]"><br>
					<span>伤害:</span><input type="input" v-model.number="Main.PlayerEditer.PlayerInfo[6]"><br>
					<span>防御:</span><input type="input" v-model.number="Main.PlayerEditer.PlayerInfo[7]"><br>
					<button id="LeftButton" type="submit" :class="['btn',this.Main.PlayerEditer.data[Main.PlayerEditer.Y][Main.PlayerEditer.X].Info[2] ? 'btn-primary' : 'btn-success','btn-sm']" style="float:left;width:45%" @click="AddPlayer">{{this.Main.PlayerEditer.data[Main.PlayerEditer.Y][Main.PlayerEditer.X].Info[2] ? "修改" : "确定"}}</button>
					<button id="RightButton" type="button" class="btn btn-danger btn-sm" style="float:right;width:45%" @click="DeletePlayer">{{this.Main.PlayerEditer.data[Main.PlayerEditer.Y][Main.PlayerEditer.X].Info[2] ? "删除" : "取消"}}</button>
			</div>
			<div id="footer">
				<textarea v-model="Main.SaveFile" style="width:500px;height:100px;"></textarea>
				<br>
				<div class="btn-group" style="padding-bottom:6px;" @click="AutoSaveF">
					<button type="button" class="btn btn-primary" @click="Main.MapEditer.ReSizeMap('left')">在左边添加一列</button>
					<button type="button" class="btn btn-primary" @click="Main.MapEditer.ReSizeMap('right')">在右边添加一列</button>
					<button type="button" class="btn btn-primary" @click="Main.MapEditer.ReSizeMap('top')">在顶部添加一行</button>
					<button type="button" class="btn btn-primary" @click="Main.MapEditer.ReSizeMap('bottom')">在底部添加一行</button>
				</div>
				<br>
				<button class="btn btn-primary" type="button" style="width:24%;" @click="LoadSave">{{Main.MapEditer.IsPlayerEdit ?  "加载玩家文件" : "加载地图"}}</button>
				<button class="btn btn-primary" type="button" style="width:24%;" @click="toggleMP">{{Main.MapEditer.IsPlayerEdit ?  "切换到地图编辑器" : "切换到玩家编辑器"}}</button>
				<br>
				<select class="form-control" style="width: 48%;margin: 4px auto 0px auto;" @change="SelectChanges" v-model="Selected">
					<option v-for="item in Selects" v-model="item.Name">{{ item.Name }}</option>
				</select>
				<button class="btn btn-primary" type="button" style="width:48.5%;margin-top:4px;" @click="LoadSavea" :disabled="!getAutoSave()">加载自动保存</button>
				<br>
				<button class="btn btn-primary" type="button" style="width:48.5%;margin-top:4px;" @click="FaBuClick">发布</button>
				<br>
				<div id="PyLink" style="margin:auto 0;">
					本站由
					<a href="https://tjidc.cf/">
						<img src="/static/images/Link.jpg" width="38px" height="38px" style="border-radius:50%"/>
						天霁云主机
					</a>
					强力驱动
				</div>
			</div>
		</div>
		<form id="FaBu" class="container-fluid center" v-show="FaBu" style="width:48.5%;" onsubmit="return false;" @submit="FaBu2">
			<input type="text" class="form-control" name="MapName" placeholder="请输入地图名称" v-model="Submit.Value" @keydown="MapInServer" @keypress="MapInServer" @keyup="MapInServer" @change="MapInServer" required autofocus/>
			<div id="captcha" style="margin:4px 4px;"></div>
			<button id="Submit" type="submit" class="btn btn-primary" style="width:100%;">{{Submit.Message}}</button>
			<button type="button" class="btn btn-primary" style="margin-top: 4px;width: 100%;" @click="FaBuClick">返回地图编辑</button>
		</form>
	</div>
</template>
<script>
import Vue from 'vue'
import MapEditer from "@/extra/index"
import axios from 'axios' //引入axios
import qs from 'qs'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
export default {
	name: 'Main',
	data () {
		return {
			Main:MapEditer,
			FaBu:false,
			Submit:{
				Message:"加载验证码",
				Value:null
			},
			Selects:[],
			Selected:null
		}
	},
	mounted (){
		var that=this;
		axios.get(location.origin+"/captcha/Captcha").then(function(data){
			window.initGeetest({
				gt: data.data.gt,
				challenge: data.data.challenge,
				offline: !data.data.success,
				new_captcha: true,
				https: true,
				width:"100%"
			}, function (captchaObj) {
				captchaObj.onReady(function(){
					that.MapInServer();
				});
				captchaObj.appendTo("#captcha");
			});
		});
		axios.get(location.origin+"/ssss/MapEditer/getmaps").then(function(data){
			that.Selects.push({Name:"Default"});
			for(var i=0,item;item=data.data[i];i++) {
				if(item.Name!="Default") {
					that.Selects.push(item);
				}
			}
			that.Selected="Default";
		});
		window.onresize=function(){
			document.getElementById("SelectBlock").style.left=(document.documentElement.clientWidth-document.getElementById("SelectBlock").clientWidth)/2+"px";
		};
	},
	methods: {
		AutoSaveF:function(){
			var Save={};
			Save.Map=this.Main.MapEditer.ToCsv(true);
			Save.Players=this.Main.PlayerEditer.ToSave(true);
			window.localStorage.AutoSave=window.JSON.stringify(Save);
		},
		initSelect:function(){
			document.getElementById("SelectBlock").style.left=(document.documentElement.clientWidth-document.getElementById("SelectBlock").clientWidth)/2+"px";
			this.Main.MapEditer.SelectStyle.left=document.querySelector('#SelectBlock > ul > li > img').offsetLeft+"px";
		},
		SelectBlock:function(x,y,e){
			this.Main.MapEditer.Select=(y*15)+x;
			this.Main.MapEditer.SelectStyle.left=e.target.offsetLeft+"px";
			this.Main.MapEditer.SelectStyle.top=e.target.offsetTop+"px";
		},
		ChangeBlock:function(x,y){
			this.Main.MapEditer.data[y][x].blockid=this.Main.MapEditer.Select;
			this.AutoSaveF();
			this.Main.MapEditer.ToCsv();
		},
		initPlayerEditer:function(){
			this.Main.PlayerEditer.Style.left=document.querySelector('#MapEditer').offsetLeft+"px";
			this.Main.PlayerEditer.Style.top=document.querySelector('#MapEditer').offsetTop+"px";
		},
		toggleMP:function(){
			Vue.set(this.Main.MapEditer,'IsPlayerEdit',this.Main.MapEditer.IsPlayerEdit ? this.Main.MapEditer.ToCsv() : this.Main.PlayerEditer.ToSave());
		},
		PlayerEdit:function(x,y,el){
			this.Main.PlayerEditer.LoadSave();
			if(!el.ctrlKey) {
				this.Main.PlayerEditer.PlayerInfo=new Array();
			} else {
				this.Main.PlayerEditer.PlayerInfo=new Array();
				if(typeof this.Main.PlayerEditer.data[this.Main.PlayerEditer.Y][this.Main.PlayerEditer.X].Info[2]!='undefined') {
					for(var i=0,item;item=this.Main.PlayerEditer.data[this.Main.PlayerEditer.Y][this.Main.PlayerEditer.X].Info[i];i++) {
						this.Main.PlayerEditer.PlayerInfo[i]=item;
					}
				}
			}
			if(typeof this.Main.PlayerEditer.data[y][x].Info[2]!='undefined') {
				this.Main.PlayerEditer.PlayerInfo=this.Main.PlayerEditer.data[y][x].Info;
			} 
			this.Main.PlayerEditer.X=x;
			this.Main.PlayerEditer.Y=y;
			var Offset=this.Main.Offset(el.target);
			this.Main.PlayerEditer.EditerStyle.left=Offset.left+64+"px";
			this.Main.PlayerEditer.EditerStyle.top=Offset.top+32+"px";
			this.Main.PlayerEditer.ShowEditer=true;
		},
		LoadSave:function(){
			if(!this.Main.MapEditer.IsPlayerEdit) {
				this.Main.MapEditer.LoadMap();
			} else {
				this.Main.PlayerEditer.LoadSave();
			}
		},
		AddPlayer:function(e){
			this.Main.PlayerEditer.AddPlayer();
			this.Main.PlayerEditer.ToSave();
			this.AutoSaveF();
		},
		DeletePlayer:function(e){
			if(e.target.textContent=="取消") {
				this.Main.PlayerEditer.ShowEditer=false;
			} else {
				this.Main.PlayerEditer.data[this.Main.PlayerEditer.Y][this.Main.PlayerEditer.X]=new this.Main.PlayerEditer.PlayerObj();
				this.Main.PlayerEditer.ShowEditer=false;
				this.Main.PlayerEditer.ToSave();
			}
			this.AutoSaveF();
		},
		FaBuClick:function(){
			this.FaBu=this.FaBu ? false : true;
			this.Submit.Value=(this.Selected!="Default"&&this.Selected!==null) ? this.Selected : null;
			this.MapInServer();
		},
		FaBu2:function(){
			if(this.Submit.Message=="发布"||this.Submit.Message=="更新") {
				var that=this;
				var Dom=document.getElementById("FaBu").getElementsByTagName("input");
				var InputData={};
				for(var i=0,item;item=Dom[i];i++) {
					InputData[item.name]=item.value;
				}
				if(!(InputData.geetest_seccode&&InputData.geetest_challenge&&InputData.geetest_validate)) {
					this.Submit.Message="请点击验证码验证";
					var tm=setTimeout(function(){
						that.MapInServer();
						clearTimeout(tm);
					},3000);
					return false;
				}
				this.Main.MapEditer.ToCsv();
				InputData.map=this.Main.SaveFile;
				this.Main.PlayerEditer.ToSave();
				InputData.players=this.Main.SaveFile;
				axios.post(location.origin+"/SSSS/MapEditer/submit",qs.stringify(InputData)).then(function(data){
					if(data.data.code==200) {
						that.Submit.Message="提交成功";
					} else {
						that.Submit.Message=data.data.message;
						var tm=setTimeout(function(){
							that.MapInServer();
							clearTimeout(tm);
						},3000);
					}
				});
			}
		},
		getAutoSave:function(){
			if(window.localStorage.AutoSave==undefined) {
				return false;
			}
			return true;
		},
		LoadSavea:function(){
			var Save=window.JSON.parse(window.localStorage.AutoSave);
			this.Main.MapEditer.LoadMap(Save.Map);
			this.Main.PlayerEditer.LoadSave(Save.Players);
			if(this.Main.MapEditer.IsPlayerEdit) {
				this.Main.PlayerEditer.ToSave();
			} else {
				this.Main.MapEditer.ToCsv();
			}
		},
		getCookie:function(cname) {
			var name = cname + "=";
			var ca = document.cookie.split(';');
			for (var i = 0; i < ca.length; i++) {
				var c = ca[i].trim();
				if (c.indexOf(name) == 0) return c.substring(name.length, c.length);
			}
			return "";
		},
		SelectChanges:function(e) {
			var val=e.target.value;
			if (val!=null) {
				var that=this;
				axios.post(location.origin+"/ssss/MapEditer/getmap",qs.stringify({Name:val})).then(function(data){
					that.Main.MapEditer.LoadMap(data.data.Map);
					that.Main.PlayerEditer.LoadSave(data.data.Players);
					if(that.Main.MapEditer.IsPlayerEdit) {
						that.Main.PlayerEditer.ToSave();
					} else {
						that.Main.MapEditer.ToCsv();
					}
				});
			}
		},
		MapInServer:function(){
			for(var i=0,item;item=this.Selects[i];i++) {
				if (this.Submit.Value==item.Name) {
					this.Submit.Message="更新";
					return true;
				}
			}
			this.Submit.Message="发布";
			return false;
		}
	}
}
</script>

<style>
	body {
		font-family: "微软雅黑","Microsoft YaHei";
		background-color:#eee!important;
		-moz-user-select: none;
		-webkit-user-select: none;
		user-select: none;
	}
	*{
		margin:0;
		padding:0;
	}
	li {
		list-style-type:none;
		border:1px solid #000;
		height:64px;
		width:64px;
		box-sizing:unset;
	}
	ul {
		display:inline-flex;
		margin-bottom:0px!important;
	}
	#MapEditer{
		float:left;
		line-height:0;
		padding-top:50px;
	}
	#PlayerEditer{
		float:left;
		line-height:0;
		padding-top:50px;
	}
	#PlayerInfoEdit {
		position: absolute;
		z-index: 2;
		top: 0px;
		left: 0px;
		width: 190px;
		height: 220px;
		color:#fff;
		background-color: #343A41!important;
		border-radius: 8px;
		padding:4px;
		line-height:1.2;
	}
	#PlayerInfoEdit input {
		width:25%;
	}
	#SelectBlock {
		position: fixed;
		top: 0;
	}
	.center{
		position:absolute;
		top:50%;
		left:50%;
		transform:translate(-50%,-50%);
		-webkit-transform:translate(-50%,-50%);
		-moz-transform:translate(-50%,-50%);
		-ms-transform:translate(-50%,-50%);
		margin-left:auto;
		margin-right:auto;
		text-align:center;
	}
</style>


